<template>
  <div class="container">
    <div class="row">
      <div class="col-md-3">
          <span class="title">
              ILLUSTRATION
          </span>
          <div v-for="item in items">
              <span> {{ item }} </span>
          </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Home',
    methods: {
        fetchItems() {
              fetch('https://jsonplaceholder.typicode.com/posts')
                .then(stream => this.items = stream.json())
                .catch(error => console.error(error))
            console.log('caca')
        }
      },
    mounted() {
        this.fetchItems()
        console.log(this.items)
    }
  }

</script>

<style lang="css">
    .Hello {
        font-size: 1.3rem;
    }
</style>



